.arrow-up--visible {
  right: 30px;
}

@media screen and (max-width: 480px) {
  .arrow-up--visible {
    right: 20px;
  }
}
@media screen and (max-width: 480px) {
  .arrow-up {
    width: 30px;
    height: 30px;
  }
}
.arrow-up a {
  outline: none;
}

